body {
    display: flex;
    background-image: linear-gradient(rgb(221, 26, 26), rgb(99, 2, 99));
    height: 100vh;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.flex {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    /* border: 1px solid white; */
}

.cover {
    z-index: 10;
    width: 200px;
    height: 200px;
    background-size: cover;
}

.cv1 {
    background-image: url("../assets/images/cover1.jpeg");
}

.cv2 {
    background-image: url("../assets/images/cover2.jpeg");
}

.cv3 {
    background-image: url("../assets/images/cover3.jpeg");
}

.vinyl {
    position: relative;
    right: 200px;
    z-index: 1;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: rgb(109, 71, 41);
    transition: transform 0.7s;
}

.detail {
    z-index: 1;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: rgb(221, 128, 57);
}

.label {
    z-index: 1;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgb(184, 104, 44);
}

.hole {
    z-index: 1;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: black;
}

